/* Estilo para o container da página Dashboard */
.container {
  display: flex;
  min-height: 100vh;
}

/* Estilo para o conteúdo principal ao lado da Sidebar */
.main-content {
  flex: 1;
  padding: 20px;
  background-color: var(--background-color);
}

/* Título da página */
h1 {
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 20px;
}

/* Estilo para o resumo de reservas (cards) */
.dashboard-summary {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.summary-card {
  background-color: var(--primary-color);
  color: white;
  padding: 20px;
  border-radius: 8px;
  width: 30%;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.summary-card h3 {
  font-size: 20px;
  margin-bottom: 10px;
}

.summary-card p {
  font-size: 16px;
  font-weight: bold;
}

/* Estilo para a tabela de reservas */
.reservations-table {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.reservations-table h2 {
  font-size: 24px;
  margin-bottom: 15px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

table th, table td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid var(--border-color);
}

table th {
  background-color: var(--primary-color);
  color: white;
}

table td {
  font-size: 14px;
}

table tr:hover {
  background-color: #f1f1f1;
}

/* Estilo para os botões de ação (Editar e Cancelar) */
.action-btn {
  padding: 5px 10px;
  border-radius: 5px;
  border: none;
  font-weight: bold;
  cursor: pointer;
}

.edit-btn {
  background-color: #4CAF50;
  color: white;
  margin-right: 10px;
}

.edit-btn:hover {
  background-color: #45a049;
}

.cancel-btn {
  background-color: #FF5722;
  color: white;
}

.cancel-btn:hover {
  background-color: #e64a19;
}

/* Estilo para o loader e mensagens de erro */
p {
  font-size: 16px;
  font-weight: bold;
  color: var(--primary-color);
}
